.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.blank {
  color: rgb(32, 8, 246);
}

.app-nav-link {
  color: #FF9A00;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
}

li.nav-item:hover {
  background-color: #f8991f;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

li.nav-item:hover a {
  color: white !important;
}

.header-height {
  height: 15vh;
}

.center-align {
  margin-top: auto;
  margin-right: 0.5rem;
  margin-bottom: auto;
  margin-left: 0.5rem;
}

.star {
  width: 137.409px;
  height: 165px;
  flex-shrink: 0;
}

.h1-app-theme{
  color: #FF9A00;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

@keyframes bounceShrink {
  0%, 100% {
    transform: translateY(0) scale(1);
  }
  50% {
    transform: translateY(-30px) scale(0.8);
  }
}

.rotate-image {
  /* ... (animation properties) */
  animation: bounceShrink 6s ease infinite;
  /* Additional properties for the static state */
  transform: translateY(0) scale(1);
}

img {
  height: auto; /* Automatically adjust the height to maintain the aspect ratio */
}

.offerings-img {
  width: 10vw;
  height: auto;
}

.offerings-txt {
  font-size: 2.7vh;
  font-family: 'Poppins';
}
.ellipse-container {
  width: 200px; /* Adjust the container width as needed */
  height: 150px; /* Adjust the container height as needed */
  overflow: hidden;
  border-radius: 50%; /* Set border-radius to 50% for an elliptical shape */
  border-color: #f8991f;
}

.ellipse-image {
  width: 100%; /* Ensure the image covers the entire container */
  height: auto; /* Automatically adjust the height to maintain the aspect ratio */
}
@media (max-width: 768px) {
  img {
    width: 80%; /* Reduce the width for screens up to 768px wide */
  }
  .offering-img {
    width: 100%;
  }  
}

@media (max-width: 576px) {
  img {
    width: 50%; /* Further reduce the width for screens up to 576px wide */
  }
  .offering-img {
    width: 50%;
  }  
}
.column {
  text-align: center; /* Horizontally center-align the contents */
  display: flex;
  flex-direction: column; /* Ensure the contents are stacked vertically */
  justify-content: center; /* Center the contents vertically */
  align-items: center; /* Center the contents horizontally */
  height: auto; /* Adjust the height as needed */
}

.industries-img {
    background: #FF9A00;
    padding: 8%;
}

.footer {
  background-color: #FDC67A;
}

.vrq-email-link{
  text-decoration: None;
}

@media (max-width: 768px) {

.star-mb{
  margin-left: 70%;
  z-index: 1;
  margin-top: -12%;
  width: 38%;
}

.offering-img-mb{
  width: 30vw;
  height: auto;
}
}